.container {
    width: 100vw;
    max-width: 100vw;
    display: grid;
    box-sizing: border-box;
    grid-template-columns: repeat(5, 1fr);
  
    grid-gap: .8rem;

    padding: 1rem 3rem 0 3rem;
}

.container > img {
    max-width: 100%;
    max-height: 500px;
    align-self: center;
    justify-self: center;
}

.modal {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 1rem;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
}

.modal > img {
    height: auto;
}

.mainHeading {
    font-size: 3em;
    animation: 2s ease-out shrinkText;
}

@keyframes shrinkText {
    from { opacity: 0; font-size: 5em; }
    to   { opacity: 1; font-size: 3em; }
  }
